.header {
  padding: 100px 0;
  display: flex;
  align-items: center;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__text-container,
  &__image-container {
    width: calc(50% - 30px);
  }

  &__buttons-container {
    margin-top: 25px;
    .button:first-of-type {
      margin-right: 10px;
    }
  }

  &__image {
    width: 100%;
    border-radius: 5px;
  }
}
.section {
  padding: 100px 0;
  &__heading {
    text-align: center;
    margin-bottom: 100px;
  }
}

.product {
  &__content {
    display: flex;
    justify-content: space-between;
    &-image {
      border-radius: 5px;
      width: calc(33.3333% - 30px);
      box-shadow: 0px 2px 10px -2px rgba(0, 0, 0, 0.4);
    }
  }
}
.howItWorks {
  &__content {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    width: calc(33.3333% - 30px);
    text-align: center;
    &-image {
      height: 100px;
      margin-bottom: 10px;
    }
    .heading-tertiary {
      margin-bottom: 20px;
    }
  }
}
.benefits {
  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__item {
    width: calc(50% - 30px);
    display: flex;

    &:nth-of-type(n + 3) {
      margin-top: 70px;
    }
    &-image {
      &-container {
      }
      width: 70px;
      margin-right: 20px;
    }
    .heading-quarternary {
      margin-bottom: 10px;
    }
  }
}

.about {
  &__image {
    border-radius: 5px;
    width: calc(50% - 15px);
    box-shadow: 0px 2px 10px -2px rgba(0, 0, 0, 0.4);
    &-container {
      display: flex;
      justify-content: space-between;
    }
  }
}

.support {
  background: url(./assets/img/support-bg.jpg) center / cover no-repeat;

  .heading-tertiary {
    max-width: 50%;
    text-transform: uppercase;
  }
}
